import React from "react";
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import "../styles/main.css";
import Gala from "../pages/Gala";
import Apsis from "../pages/Apsis";
import Axon from "../pages/Axon";
import Me from "../pages/Me";
import Error from "../pages/Error";
import Home from "../pages/Home";

const MainLayout = () => (
  <Router>
    <Route path={`/:projectId`} component={Trick} />
    <div className="mainLayout">
      <Route component={Nav} />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/axon" component={Axon} />
        <Route path="/apsis" component={Apsis} />
        <Route path="/gala" component={Gala} />
        <Route path="/me" component={Me} />
        <Route component={Error} />
      </Switch>
    </div>
  </Router>
);

const Trick = ({ match }) => (
  <div className={`trick ${match.params.projectId}`} />
);

const Nav = ({ match }) => (
  <nav>
    <ul>
      <li>
        <ActivateMenuLink exact to="/" label="Home" icon="arrow-right" />
      </li>
      <li>
        <span>Projects</span>
        <ul>
          <li>
            <ActivateMenuLink to="/axon" label="Axon" icon="arrow-right" />
          </li>
          <li>
            <ActivateMenuLink to="/apsis" label="Apsis" icon="arrow-right" />
          </li>
          <li>
            <ActivateMenuLink to="/gala" label="Gala" icon="arrow-right" />
          </li>
        </ul>
      </li>
      <li>
        <ActivateMenuLink to="/me" label="Me" icon="arrow-right" />
      </li>
    </ul>
  </nav>
);

const ActivateMenuLink = ({
  label,
  to,
  activeOnlyWhenExact,
  icon,
  className
}) => (
  <Route
    path={to}
    exact={activeOnlyWhenExact}
    children={({ match }) => (
      <Link className={`${match ? "active" : ""} ${className || ""}`} to={to}>
        {icon ? <i className={`icon ${icon}`} /> : null}
        {label}
      </Link>
    )}
  />
);

export default MainLayout;
