import React from "react";
import projects from "../data/projects";

export default class Card extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className="card">
        <ul>
          <li>
            <strong>{projects.client}</strong>
            {this.props.client}
          </li>
          <li>
            <strong>{projects.time}</strong>
            {this.props.time}
          </li>
          <li>
            <strong>{projects.role}</strong>
            <span>{this.props.role.one}</span>
            {this.props.role.two ? <span>{this.props.role.two}</span> : null}
            {this.props.role.three ? (
              <span>{this.props.role.three}</span>
            ) : null}
          </li>
          <li>
            <strong>{projects.team}</strong>
            <span>{this.props.team.one}</span>
            {this.props.team.two ? <span>{this.props.team.two}</span> : null}
            {this.props.team.three ? (
              <span>{this.props.team.three}</span>
            ) : null}
          </li>
        </ul>
      </div>
    );
  }
}
