import React from "react";
import Page from "../components/Page";
import home from "../data/home";

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  render() {
    return (
      <Page
        title={home.title}
        span={home.span}
        subtitle={home.subtitle}
        className="home"
      >
        <p className="display">
          {home.displayText} <strong>{home.displayStrong}</strong>
        </p>
      </Page>
    );
  }
}
